body {
    background-color: #eda285;
    padding: 20px;
    margin: 0;
  }
  h1{
    color: #314260; 
    text-align: center;
    font-family: 'Trebuchet MS', sans-serif;
    font-size: 5em;
    text-decoration-line: line-through;
    text-decoration-color: #6c7d9b;
    
  }
  
  h2{
    color: #314260; 
    text-align: center;
    font-family: 'Trebuchet MS', sans-serif;
    font-size: 3em;
  }
   p{
     
    font-family: sans-serif;
    color: #FFF;
    text-align: center;
    position: relative;
    padding-left: 15%;
    padding-right: 15%;
    padding-bottom: 3%;
    padding-top: 3%;
   }
   
   ul, li {
    text-align: center;
    font-family: sans-serif;
    color: #6c7d9b;
  }
  ul.header li {
    display: inline;
    list-style-type: none;
    margin: 0;
    
  }
  ul.header {
   
    padding: 0;
  }
  ul.header li a {
    color: #FFF;
    font-weight: bold;
    text-decoration: none;
    padding: 20px;
    display: inline-block;
    
  }
  .content {
    
    padding: 20px;
    
  }
  .content h2 {
    padding: 0;
    margin: 0;
  }
  .content li {
    margin-bottom: 10px;
  }
  .active {
    background-color: #6c7d9b;
  }
  
  img{
    width: 100%;
    height: auto;
  }